.active__orders {
  width: 100%;
  /* width: 33.75rem; */
  padding: 1.25rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.active__orders__inner {
  height: 100%;
  max-height: 37.5rem;
}

.card__inner {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.card__header {
  min-width: 100%;
  padding-bottom: 1.25rem;
  border-bottom: 0.0625rem solid #696897;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card__title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #4643d2;
}

.card__status {
  font-size: 1rem;
  color: #4643d2;
}
.card__created {
  font-size: 1rem;
  color: #4643d2;
}
.card__done {
  font-size: 1rem;
  color: #696897;
}
.card__cansel {
  font-size: 1rem;
  color: #fe805f;
}
.card__content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card__content__top,
.card__content__bottom {
  width: 100%;
  margin-top: 1.25rem;
  display: flex;
  justify-content: flex-start;
  align-items: start;
}

.card__order__type {
  font-size: 1.25rem;
  font-weight: 700;
}

.card__order__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.card__order__weight,
.card__order__time {
  padding: 5px 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.order__weight__text,
.order__time__text {
  margin: 0 0.3125rem;
  font-size: 1.125rem;
}

.order__weight__value,
.order__time__value {
  font-size: 1.25rem;
  font-weight: 700;
}

.card__order__adress {
  font-size: 1.25rem;
  font-weight: 700;
}

.card__order__adress__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card__order__from__to {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.card__order__from,
.card__order__to {
  font-size: 1.25rem;
}

.card__order__connector__img {
  padding: 0.625rem;
}

.card__order__adress {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.card__order__start__adress,
.card__order__end__adress {
  text-align: left;
  font-size: 1.25rem;
  font-weight: 700;
}

.card__price {
  width: 100%;
  margin-top: 0.625rem;
  text-align: left;
  font-size: 1.5rem;
  font-weight: 700;
  color: #4643d2;
}

@media only screen and (max-width: 700px) {
  .card__order__connector__img {
    height: 3.5rem;
  }

  .card__order__type {
    margin-right: 10px;
  }

  .card__order__start__adress,
  .card__order__end__adress {
    font-size: 1.1rem;
  }

  .card__order__from,
  .card__order__to {
    font-size: 1.1rem;
  }
}
